<template>
  <div>
    <span style="position:relative;top:10px;left:60px;color: #409EFF;font-size: 16px">安徽省空气质量排名情况</span>
    <div id="main" style="width:1600px;height: 100vh;position: relative;right: 100px;bottom: 40px"></div>
  </div>

</template>

<script>
import * as echarts from 'echarts';

export default {
  name: "Time_SpaceAnalysis",
  mounted() {
    this.initecharts()
  },
  methods:{
    initecharts(){
      //获取盛放容器
      var chartDom = document.getElementById('main');
      //echarts初始化
      var myChart = echarts.init(chartDom);
      var option;

      //数据data
      const data = [];
      //data.push是填入数据 这个循环是为data塞入数据
      for (let i = 0; i < 17; ++i) {
        data.push(Math.round(Math.random() * 200));
      }
      option = {
        xAxis: {
          max: 'dataMax',
        },
        yAxis: {
          type: 'category',
          data: ['滁州市', '黄山市', '铜陵市', '马鞍山市', '安庆市','芜湖市','阜阳市','六安市','亳州市','宿州市','芜湖市','合肥市','六安市','宣城市','蚌埠市','淮南市','淮北市'],
          inverse: true,
          animationDuration: 300,
          animationDurationUpdate: 300,
          max: 16   //展示的柱状图数量是这个数字+1
        },

        series: [
          {
            realtimeSort: true,
            name: 'X',
            type: 'bar',
            data: data,
            label: {
              show: true,
              position: 'right',  //展示位置
              valueAnimation: true //数据过渡平滑化
            }
          }
        ],
        legend: {
          show: false//不展示图例
        },

        animationDuration: 0,
        animationDurationUpdate: 3000,
        animationEasing: 'linear',
        animationEasingUpdate: 'linear'
      };

      function run() {
        for (var i = 0; i < data.length; ++i) {
          if (Math.random() > 0.9) {  //当math的数据数大于0.9时这个数字*0.9
            data[i] += Math.round(Math.random() * 2000);
          } else {
            data[i] += Math.round(Math.random() * 200);
          }
        }

        myChart.setOption({
          series: [
            {
              type: 'bar',
              data
            }
          ]
        });
      }
      //当页面第一次出来时
      setTimeout(function () {
        run();
      }, 0);
      //每3秒钟变化一次
      setInterval(function () {
        run();
      }, 3000);

      option && myChart.setOption(option);

    }
  }
}
</script>


